<script>
import {postRequest,getRequest} from '@/utils/request';


export default {
  name: 'home',
  data() {
    
    const modelRef = {
        account: '',
        password: ''
    }

    return {
      labelCol: {
        span: 6,
      },
      wrapperCol: {
        span: 14,
      },
      modelRef,
      stateTxt: "login",

    };
  },
  methods:{
    changeState(){
      if(this.stateTxt === 'login'){
        this.stateTxt ='register'
      }else{
        this.stateTxt = 'login'
      }
    },
    async test666(){
      const res=await postRequest( `/wxk/pic`,{test:'333333333333333333'})
      console.log(res,'res')
    },
    /* 登录或者注册 */
    async toFuns(){
       const modelRef = this.modelRef
       const userInfo = {
         userName: modelRef.account,
         passWord:  modelRef.password
       }
       try{
         let inter = this.stateTxt === 'login'? 'login' : 'register'
         const {token, userId} = await postRequest( `/user/${inter}`,userInfo)
         sessionStorage.setItem('token', token)
         sessionStorage.setItem('userId', userId)
         this.$router.push({
            path: '/img-projects',
            query: {userId},
         });
       }catch(err){
         console.log(err, 'err')
       }
    }
  }
};
</script>

